<script>
	import {section} from "../../common/section.js";
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: {},
		mounted() {
			
		},

		data() {
			return {
				projectId:'',
				active: '',
				packageName:'',//套餐名
				monthStandard:'',//包月金额
				monthNum:'',//包月次数/时长（小时）
				comeStandard:0,//加油包金额
				comeNum:0,//加油包次数/时长
				remarks:'',//备注
				numtitle:1,//允许最大购买次数
			}
		},
		onLoad(options) {
			this.projectId=options.projectId;
		},
		methods: {
			//添加一个费率套餐
			xinzbtn(){
				let data={}
				if(this.active==0){
					if(!this.packageName){
						this.$toast.fail('套餐名不能为空')
						return;
					}
					data={
						packageName:this.packageName,
						type:'A3',
						minMoney:0,
						lossRate:0,
						isFund:0,
						remarks:this.remarks,
						monthStandard:this.monthStandard,
						monthNum:this.monthNum,
						comeStandard:this.comeStandard,
						comeNum:this.comeNum,
						limitNum:this.numtitle
					}
					
				}else if(this.active==1){
					
					if(!this.packageName){
						this.$toast.fail('套餐名不能为空')
						return;
					}
					
					data={
						packageName:this.packageName,
						type:'A1',
						minMoney:0,
						lossRate:0,
						isFund:0,
						remarks:this.remarks,
						monthStandard:this.monthStandard,
						monthNum:this.monthNum,
						comeStandard:this.comeStandard,
						comeNum:this.comeNum,
						limitNum:this.numtitle
					}
				}
				
				let header={
					'content-type': 'application/json '
				}
				this.$base.request1('manager/rate'+'/'+this.projectId, 'POST', data,header)
				.then(res => {
					if(res.data.code==200){
						this.$toast.success('添加成功')
						uni.navigateBack()
					}else{
						this.$toast.fail(res.data.msg)
						return;
					}
				})
				.catch(err => {
					
				})
				
				
			},
			onClick(){
				this.packageName='',
				this.remarks='',
				this.monthStandard='',
				this.monthNum='',
				this.comeStandard=0,
				this.comeNum=0,
				this.numtitle=1
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			}
			
			
		}
	}
</script>

